<template>
	<div>
		<Loader v-if="isLoading" />
		<v-data-table
			class="mt-6"
			:headers="header"
			:items="dataKelas"
			:items-per-page="10">
			<template v-slot:no-data>
	      <v-alert type="info" class="mt-4">
	        <div>Data tidak ditemukan</div>
	      </v-alert>
	    </template>
	    <template v-slot:item.no="{item}">
	    	{{dataKelas.map(x => x.id).indexOf(item.id)+1}}    	
	    </template>
			<template v-slot:item.action="{item}">
				<v-btn icon color="success">
					<v-icon small>mdi-eye</v-icon>
				</v-btn>
				<EditKelas :data="item" />
				<HapusKelas :id="item.id" />
			</template>		
		</v-data-table>
	</div>
</template>

<script>
	import { mapState } from 'vuex'
	import EditKelas from '@/components/Kelas/EditKelas'
	import HapusKelas from '@/components/Kelas/HapusKelas'
	import Loader from '@/components/Loader'

	export default{
		data: () => ({
			header: [
				{ text: 'No', value: 'no', sortable: false },
				{ text: 'Nama', value: 'nama', sortable: false },
				{ text: 'Action', value: 'action', align: 'center', sortable: false }
			],

			data: [
				{
					nama: 'X',
					id: 1
				}
			],
		}),

		components: {
			Loader,
			HapusKelas,
			EditKelas
		},

		created() {
			this.$store.dispatch('dataKelas/getDataKelas')
			this.$store.dispatch('dataKelas/updateIsLoading', true)
		},

		computed: { ...mapState('dataKelas', ['dataKelas', 'isLoading']) }
	}
</script>